main {
  width: 95%;
  margin: 20% auto;
  background-color: var(--darkGraysihBlue);
  border-radius: 0.9375rem;
  text-align: center;
  padding: 1.5625rem;
  position: relative;
}
@media (min-width: 71.875em) {
  main {
    width: 40%;
    margin: 10% auto;
  }
}
main .card__adviceId {
  padding: 1.25rem;
}
main .card__adviceId span {
  font-size: 0.9375rem;
  color: var(--neonGreen);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
main .card__advice {
  margin-top: 0.9375rem;
  text-align: center;
  word-spacing: 2px;
}
main .card__advice h1 {
  color: var(--lightCyan);
  font-size: 1.75rem;
  line-height: 1.6;
}
main .card__divider {
  margin: 1.125rem 0 2.5rem 0;
}
main .card__divider img {
  max-width: 100%;
  height: auto;
}
main .card__button {
  position: absolute;
  bottom: -1.75rem;
  left: 0;
  right: 0;
}
main .card__button button {
  padding: 1.125rem;
  border-radius: 50%;
  border: none;
  background-color: var(--neonGreen);
}
main .card__button button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 24px 2px var(--neonGreen);
  transition: all 0.3s ease-in-out;
}
main .card__button button img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}

:root {
  /* Primary Colors: */
  --lightCyan: hsl(193, 38%, 86%);
  --neonGreen: hsl(150, 100%, 66%);
  /* Neutral Colors: */
  --grayishBlue: hsl(217, 19%, 38%);
  --darkGraysihBlue: hsl(217, 19%, 24%);
  --darkBlue: hsl(218, 23%, 16%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background-color: var(--darkBlue);
}